"use client";
import {
  Box,
  Button,
  Container,
  Fade,
  FormControl,
  InputLabel,
  MenuItem,
  Paper,
  Select,
  SelectChangeEvent,
  Slide,
  Stack,
  Step,
  StepLabel,
  Stepper,
  TextField,
  Typography,
} from "@mui/material";
import React, { useState } from "react";
import Navbar from "../../components/Navbar";

// Types
interface PersonalDetailsData {
  fullName: string;
  dateOfBirth: string;
  gender: string;
  email: string;
  address: string;
}

interface AcademicDetailsData {
  tenth: {
    schoolName: string;
    board: string;
    yearOfPassing: string;
    percentage: string;
  };
  twelfth: {
    schoolName: string;
    board: string;
    yearOfPassing: string;
    percentage: string;
  };
  graduation: {
    collegeName: string;
    university: string;
    degree: string;
    percentage: string;
  };
}

interface DocumentsData {
  photo: File | null;
  tenthMarksheet: File | null;
  twelfthMarksheet: File | null;
  graduationMarksheet: File | null;
}

const steps: string[] = [
  "Personal Details",
  "Academic Details",
  "Documents Upload",
];

const AdmissionForm: React.FC = () => {
  const [activeStep, setActiveStep] = useState<number>(0);
  const [personalDetails, setPersonalDetails] = useState<PersonalDetailsData>({
    fullName: "",
    dateOfBirth: "",
    gender: "",
    email: "",
    address: "",
  });

  const [academicDetails, setAcademicDetails] = useState<AcademicDetailsData>({
    tenth: {
      schoolName: "",
      board: "",
      yearOfPassing: "",
      percentage: "",
    },
    twelfth: {
      schoolName: "",
      board: "",
      yearOfPassing: "",
      percentage: "",
    },
    graduation: {
      collegeName: "",
      university: "",
      degree: "",
      percentage: "",
    },
  });

  const [documents, setDocuments] = useState<DocumentsData>({
    photo: null,
    tenthMarksheet: null,
    twelfthMarksheet: null,
    graduationMarksheet: null,
  });

  const [error, setError] = useState<string>("");

  const handleNext = (): void => {
    setActiveStep((prevStep) => prevStep + 1);
  };

  const handleBack = (): void => {
    setActiveStep((prevStep) => prevStep - 1);
  };

  const handlePersonalDetailsChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ): void => {
    const { name, value } = event.target;
    setPersonalDetails((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleGenderChange = (event: SelectChangeEvent<string>): void => {
    setPersonalDetails((prev) => ({
      ...prev,
      gender: event.target.value,
    }));
  };

  const handleFileChange = (
    event: React.ChangeEvent<HTMLInputElement>,
    field: keyof DocumentsData,
  ): void => {
    const file = event.target.files?.[0] || null;
    setDocuments((prev) => ({
      ...prev,
      [field]: file,
    }));
  };

  const PersonalDetails: React.FC = () => (
    <Fade in={activeStep === 0} timeout={500}>
      <Stack spacing={3}>
        <Stack direction={{ xs: "column", sm: "row" }} spacing={2}>
          <TextField
            required
            fullWidth
            name="fullName"
            label="Full Name"
            variant="outlined"
            value={personalDetails.fullName}
            onChange={handlePersonalDetailsChange}
          />
          <TextField
            required
            fullWidth
            name="dateOfBirth"
            type="date"
            label="Date of Birth"
            variant="outlined"
            value={personalDetails.dateOfBirth}
            onChange={handlePersonalDetailsChange}
            //defaultValue=""
            InputLabelProps={{ shrink: true }}
          />
        </Stack>
        <Stack direction={{ xs: "column", sm: "row" }} spacing={2}>
          <FormControl fullWidth>
            <InputLabel>Gender</InputLabel>
            <Select
              value={personalDetails.gender}
              label="Gender"
              onChange={handleGenderChange}
            >
              <MenuItem value="male">Male</MenuItem>
              <MenuItem value="female">Female</MenuItem>
              <MenuItem value="other">Other</MenuItem>
            </Select>
          </FormControl>
          <TextField
            required
            fullWidth
            name="email"
            label="Email"
            type="email"
            value={personalDetails.email}
            onChange={handlePersonalDetailsChange}
          />
        </Stack>
        <TextField
          required
          fullWidth
          name="address"
          label="Address"
          multiline
          rows={3}
          value={personalDetails.address}
          onChange={handlePersonalDetailsChange}
        />
      </Stack>
    </Fade>
  );

  const AcademicDetails: React.FC = () => (
    <Fade in={activeStep === 1} timeout={500}>
      <Stack spacing={3}>
        <Typography variant="h6">10th Standard Details</Typography>
        <Stack direction={{ xs: "column", sm: "row" }} spacing={2}>
          <TextField
            required
            fullWidth
            label="School Name"
            value={academicDetails.tenth.schoolName}
            onChange={(e) =>
              setAcademicDetails((prev) => ({
                ...prev,
                tenth: { ...prev.tenth, schoolName: e.target.value },
              }))
            }
          />
        </Stack>

        <Typography variant="h6">12th Standard Details</Typography>
        <Stack direction={{ xs: "column", sm: "row" }} spacing={2}>
          <TextField
            required
            fullWidth
            label="School Name"
            value={academicDetails.twelfth.schoolName}
            onChange={(e) =>
              setAcademicDetails((prev) => ({
                ...prev,
                twelfth: { ...prev.twelfth, schoolName: e.target.value },
              }))
            }
          />
        </Stack>

        <Typography variant="h6">Graduation Details</Typography>
        <Stack direction={{ xs: "column", sm: "row" }} spacing={2}>
          <TextField
            required
            fullWidth
            label="College Name"
            value={academicDetails.graduation.collegeName}
            onChange={(e) =>
              setAcademicDetails((prev) => ({
                ...prev,
                graduation: { ...prev.graduation, collegeName: e.target.value },
              }))
            }
          />
        </Stack>
      </Stack>
    </Fade>
  );

  const DocumentUpload: React.FC = () => (
    <Fade in={activeStep === 2} timeout={500}>
      <Stack spacing={3}>
        <Typography variant="h6">Upload your Photograph</Typography>
        <TextField
          required
          fullWidth
          type="file"
          label="Photo"
          InputLabelProps={{ shrink: true }}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            handleFileChange(e, "photo")
          }
        />
        <Typography variant="h6">Upload your 10th Marksheet</Typography>
        <TextField
          required
          fullWidth
          type="file"
          label="10th Marksheet"
          InputLabelProps={{ shrink: true }}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            handleFileChange(e, "tenthMarksheet")
          }
        />
        <Typography variant="h6">Upload your 12th Marksheet</Typography>
        <TextField
          required
          fullWidth
          type="file"
          label="12th Marksheet"
          InputLabelProps={{ shrink: true }}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            handleFileChange(e, "twelfthMarksheet")
          }
        />
        <Typography variant="h6">Upload your Graduation Marksheet</Typography>
        <TextField
          required
          fullWidth
          type="file"
          label="Graduation Marksheet"
          InputLabelProps={{ shrink: true }}
          inputProps={{ accept: "application/pdf" }}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
            const file = e.target.files?.[0];
            if (file && file.type !== "application/pdf") {
              setError("Please upload only PDF files");
              e.target.value = "";
              return;
            }
            handleFileChange(e, "graduationMarksheet");
          }}
        />
        {error && (
          <Slide direction="up" in={!!error}>
            <Paper
              sx={{ p: 2, bgcolor: "error.light", color: "error.contrastText" }}
            >
              <Typography>{error}</Typography>
            </Paper>
          </Slide>
        )}
      </Stack>
    </Fade>
  );

  const getStepContent = (step: number): React.ReactNode => {
    switch (step) {
      case 0:
        return <PersonalDetails />;
      case 1:
        return <AcademicDetails />;
      case 2:
        return <DocumentUpload />;
      default:
        return "Unknown step";
    }
  };

  const handleSubmit = (): void => {
    // Handle form submission
    console.log({
      personalDetails,
      academicDetails,
      documents,
    });
  };

  return (
    <>
      <Navbar />
      <Container maxWidth="md" sx={{ my: 4 }}>
        <Fade in timeout={1000}>
          <Paper elevation={3} sx={{ p: 4 }}>
            <Typography variant="h4" align="center" gutterBottom>
              MCA Admission Application
            </Typography>

            <Stepper activeStep={activeStep} sx={{ my: 4 }}>
              {steps.map((label) => (
                <Step key={label}>
                  <StepLabel>{label}</StepLabel>
                </Step>
              ))}
            </Stepper>

            {activeStep === steps.length ? (
              <Fade in timeout={500}>
                <Box>
                  <Typography>Application submitted successfully!</Typography>
                </Box>
              </Fade>
            ) : (
              <>
                {getStepContent(activeStep)}
                <Box
                  sx={{ display: "flex", justifyContent: "flex-end", mt: 3 }}
                >
                  <Button
                    disabled={activeStep === 0}
                    onClick={handleBack}
                    sx={{ mr: 1 }}
                  >
                    Back
                  </Button>
                  <Button
                    variant="contained"
                    onClick={
                      activeStep === steps.length - 1
                        ? handleSubmit
                        : handleNext
                    }
                  >
                    {activeStep === steps.length - 1 ? "Submit" : "Next"}
                  </Button>
                </Box>
              </>
            )}
          </Paper>
        </Fade>
      </Container>
    </>
  );
};

export default AdmissionForm;
